"use client";
import { getUserData } from "@/api/auth.api";
import { deleteNote, getNotes } from "@/api/note.api";
import UpdateNote from "@/components/UpdateNote";
import { useStore } from "@/contexts/context";
import React, { useEffect, useState } from "react";

const page = () => {
  const [isUpdate, setIsUpdate] = useState(false);
  const [noteUpdateData, setNoteUpdateData] = useState<{
    title: string;
    content: string;
  }>({
    title: "",
    content: "",
  });
  const [isRender, setIsRender] = useState(false);
  const { myDetails, setMyDetails, setNoteLists, noteLists } = useStore();

  const handelDelete = async (id: string) => {
    if (await deleteNote({ id })) {
      setIsRender((prv) => !prv);
      setNoteLists(await getNotes({ page: 1, user: myDetails.username }));
    }
  };

  useEffect(() => {
    const setUser = async () => {
      if (!myDetails) {
        setMyDetails(await getUserData());
      }
    };
    setUser();
    return () => {};
  }, []);
  useEffect(() => {
    const setNote = async () => {
      if (myDetails) {
        setNoteLists(await getNotes({ page: 1, user: myDetails.username }));
      }
    };
    setNote();
    return () => {};
  }, [myDetails]);
  return (
    <div className="flex flex-col items-center justify-start min-h-screen bg-gradient-to-br from-green-600 to-blue-500">
      <div className="w-96 h-14 relative mt-20 text-black rounded-lg border border-white bg-blue-400">
        <input
          type="text"
          placeholder="Search by username"
          className="h-full text-xl w-3/4 bg-transparent pl-3 rounded-lg outline-none"
        />
        <button className="h-full w-1/4 rounded-lg p-1 bg-green-300">
          submit
        </button>
      </div>
      <div className="mt-10">
        <span className="underline cursor-pointer">Create Note</span>
      </div>
      <UpdateNote />
      <div className="flex flex-wrap mt-7 gap-4">
        {noteLists.map((item, idx) => (
          <div
            key={idx}
            className="p-2 relative flex flex-col flex-wrap items-center rounded-lg bg-red-400"
          >
            <h1 className="text-2xl">{item.title}</h1>
            <p>{item.content}</p>
            <span className="absolute top-3 right-3 underline text-blue-600">
              Edit
            </span>
            <span
              onClick={() => handelDelete(item._id)}
              className="absolute top-3 left-3 underline text-blue-600"
            >
              Delete
            </span>
          </div>
        ))}
      </div>
    </div>
  );
};

export default page;
